import { Component, OnInit } from '@angular/core';
import { VendorVendorAuditService } from 'src/app/sdk/asset/services';
import { Vendor_audit } from 'src/app/sdk/asset/models';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private vendorVendorAuditService: VendorVendorAuditService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService
    ) { }

    id_vendor: string = ''

    ngOnInit() {
        this.getData()
    }


    // sort(sort: { key: string; value: string }): void {
    //     this.sortKey = sort.key
    //     this.sortValue = sort.value
    //     this.getData()
    // }


    sortValue: string | null = null
    sortKey: string | null = null
    search: string = ''
    listOfDisplayData: Vendor_audit[] = []
    listOfData: Vendor_audit[] = []
    getData() {
        this.spinner.show()
        this.vendorVendorAuditService.getVendorVendorAudits(JSON.stringify({ sortKey: this.sortKey, sortValue: this.sortValue,search: this.search })).subscribe(
            data => {
                this.listOfDisplayData = data.data['all'];
                this.listOfData = data.data['all'];
                this.spinner.hide()

            }
        )
    }

    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.getData()
    }



    updateFilter(value: string): void {
        this.search = value
        this.getData()
    }



    delete(id) {
        this.spinner.show();
        this.vendorVendorAuditService.deleteVendorVendorAuditsId(id).subscribe(
            data => {
                this.getData();
                this.spinner.hide();
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel');
    }

}
