import { Component, OnInit } from '@angular/core';
import { VendorVendorAuditService, VendorVendorService } from 'src/app/sdk/asset/services';
import { Vendor_audit, Vendor } from 'src/app/sdk/asset/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private vendorVendorAuditService: VendorVendorAuditService,
        private vendorVendorService: VendorVendorService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router

    ) { }

    dataAudit: Vendor_audit

    listVendor: Vendor[] = []



    ngOnInit() {
        this.form()
        this.getPreeData()

    }

    getPreeData() {
        this.spinner.show()
        this.vendorVendorService.getVendorVendors().subscribe(data => {
            this.listVendor = data.data
            this.spinner.hide()
        })
    }


    dataForm: FormGroupTypeSafe<Vendor_audit>
    form() {
        this.dataForm = this.fb.group<Vendor_audit>(
            {
                tanggal_audit: new FormControl('', [Validators.required]),
                id_vendor: new FormControl('', [Validators.required]),
                status: new FormControl('')
            }
        )
    }


    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.dataAudit = this.dataForm.value;
        this.vendorVendorAuditService.postVendorVendorAudits(this.dataAudit).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/vendor/audit/list'])
    }
}
