import { Component, OnInit } from '@angular/core';
import { Vendor_audit, Vendor, Contact } from 'src/app/sdk/asset/models';
import { VendorVendorAuditService, VendorVendorService } from 'src/app/sdk/asset/services';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';
import { Kantor, Perizinan } from 'src/app/sdk/hr/models';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private vendorVendorAuditService: VendorVendorAuditService,
        private vendorVendorService: VendorVendorService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) { }

    data: Vendor_audit = {};
    toogleUpload = false
    id_vendor_audit: string = "";
    ngOnInit() {
        this.id_vendor_audit = this.activatedRoute.snapshot.paramMap.get('id_vendor_audit');
        this.form();
        this.getPreeData()
        this.getData()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'vendor_audit.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )

    }

    togleShowUpload() {

        this.toogleUpload = true;
    }


    getData() {
        this.spinner.show()
        this.vendorVendorAuditService.getVendorVendorAuditsId(this.id_vendor_audit).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()
                this.singleUploadService.param.next({
                    for_module: "vendor_audit",
                    for_table: "erp_vendor.vendor_audit",
                    for_module_id: data.data.id_vendor_audit
                })
            }
        )

    }



    listVendor: Vendor[] = [];
    getPreeData() {
        this.spinner.show()
        this.vendorVendorService.getVendorVendors().subscribe(data => {
            this.listVendor = data.data
            this.spinner.hide()
        })
    }



    dataForm: FormGroupTypeSafe<Vendor_audit>;
    form() {
        this.dataForm = this.fb.group<Vendor_audit>(
            {
                id_vendor: new FormControl(''),
                id_vendor_audit: new FormControl(''),
                id_dokumen: new FormControl(''),
                status: new FormControl(''),
                tanggal_audit: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.vendorVendorAuditService.putVendorVendorAuditsId({
            id: this.id_vendor_audit,
            body: this.data
        }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/vendor/audit/list'])
    }

}
